<template>
  <div class="page-container">
    <el-container class="page-container scrollAllY">
      <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
        <!---->
        <el-form class="queryForm" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size">
          <el-row class="elCollapseDict">
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.serialName" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.secoundName" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.tabName" placeholder="" clearable></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="elDeptFormButton dictQueryCenterButton">
            <el-col :span="24">
              <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:tsysserialno:query" type="primary" @click="findPage(null)"/>
              <cb-button icon="fa fa-plus" :label="$t('action.add')" perms="sys:tsysserialno:add" type="primary" @click="handleAdd" />
            </el-col>
          </el-row>
        </el-form>
      </el-header>
      <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
        <!---->
        <cb-table permsEdit="sys:tsysserialno:edit" permsDelete="sys:tsysserialno:remove" :isOpen="isOpen"
                  :data="pageResult" :columns="columns" @findPage="findPage" @handleEdit="handleEdit" @handleDelete="handleDelete">
        </cb-table>
      </el-main>
    </el-container>
    <!---->
    <el-dialog v-dialogDrag :title="operation?'':''" width="50%" :visible.sync="dialogVisible" :close-on-click-modal="false">
      <el-form :model="dataForm" label-width="100px" ref="dataForm" :rules="dataSerialno"
               :size="size" style="display: flex;height: 250px;margin-top: 10px">
          <div style="width: 50%;">
            <el-form-item label="ID" prop="roleId" v-if="false">
            <el-input v-model="dataForm.roleId" :disabled="true" auto-complete="off"></el-input>
          </el-form-item>
            <el-form-item label="" prop="serialName">
              <el-input v-model="dataForm.serialName" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="formularRegx">
              <el-input v-model="dataForm.formularRegx" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="serialLength">
              <el-input-number v-model="dataForm.serialLength" controls-position="right" :min="1" label="" style="width:100%;">
              </el-input-number>
            </el-form-item>
            <el-form-item label="" prop="initValue">
              <el-input v-model="dataForm.initValue" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="tabName">
              <el-input v-model="dataForm.tabName" auto-complete="off"></el-input>
            </el-form-item>
          </div>
        <div style="width: 50%;margin-right: 40px">
          <el-form-item label="" prop="secoundName">
            <el-input v-model="dataForm.secoundName" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="createType">
            <el-select v-model="dataForm.createType" placeholder="" style="width: 100%">
              <el-option
                v-for="data in datacreateType"
                :key="data.value"
                :label="data.label"
                :value="data.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="step">
            <el-input-number v-model="dataForm.step" controls-position="right" :min="1" label="" style="width:100%;">
            </el-input-number>
          </el-form-item>
          <el-form-item label="" prop="currentValue">
            <el-input v-model="dataForm.currentValue" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="sfbl">
            <el-select v-model="dataForm.sfbl" placeholder=""  style="width: 100%">
              <el-option
                v-for="data in datasfbl"
                :key="data.value"
                :label="data.label"
                :value="data.value">
              </el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
      <el-form :model="dataForm" label-width="100px"  :size="size">
        <el-form-item label="" prop="remark">
          <el-input type="textarea" v-model="dataForm.remark" auto-complete="off" ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click.native="dialogVisible = false">{{$t('action.cancel')}}</el-button>
        <el-button :size="size" type="primary" @click.native="submitForm"  :loading="editLoading">{{$t('action.preservation')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import { format } from "@/utils/datetime"
  export default {
    components:{
      CbTable,
      CbButton
    },
    data() {
      return {
        dataSerialno: {
          serialName: [{ required: true, message: "", trigger: "blur" }],
          formularRegx: [{ required: true, message: "", trigger: "blur" }],
          serialLength: [{ required: true, message: "", trigger: "blur" }],
          initValue: [{ required: true, message: "", trigger: "blur" }],
          tabName: [{ required: true, message: "", trigger: "blur" }],
          secoundName: [{ required: true, message: "", trigger: "blur" }],
          currentValue: [{ required: true, message: '', trigger: 'blur' }]
        },
        size: 'small',
        filters: {
          label: ''
        },
        isOpen : true, // 
        dataForm:{
          serialName: '',
          formularRegx: '',
          serialLength: '',
          initValue: '',
          tabName: '',
          secoundName: '',
          createType: '',
          currentValue: '',
          sfbl: '',
          step: '',
          remark: '',
        },
        columns: [
          {prop:"serialName", label:"", minWidth:70},
          {prop:"formularRegx", label:"", minWidth:70},
          {prop:"serialLength", label:"", minWidth:70},
          {prop:"initValue", label:"", minWidth:70},
          {prop:"tabName", label:"", minWidth:70},
          {prop:"secoundName", label:"", minWidth:70},
          {prop:"currentValue", label:"", minWidth:70},
          {prop:"remark", label:"", minWidth:70},
          {prop:"step", label:"", minWidth:70}
        ],
        pageResult: {},
        datasfbl: [{value: '1',label: ''}, {value: '0',label: ''}],
        datacreateType: [{value: '1',label: ''}, {value: '2',label: ''},
          {value: '3',label: ''}, {value: '4',label: ''},{value: '5',label: ''}],
        value: '',
        dialogVisible: false,
        operation: false, // true:, false:
        editDialogVisible: false, // 
        editLoading: false,
      }
    },
    methods: {
      // 
      findPage: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        let jj={};
        jj.pageNum=this.pageRequest.pageNum;
        jj.pageSize=this.pageRequest.PageSize;
        jj.serialName=this.filters.serialName;
        jj.secoundName=this.filters.secoundName;
        jj.tabName=this.filters.tabName;
        this.$api.serialno.findPage(jj).then((res) => {
          this.pageResult = res.data
        }).then(data!=null?data.callback:'')

      },
      // 
      handleDelete: function (data) {
        let arr = [];
        for (let i=0; i<data.params.length; i++){
          arr.push(data.params[i].id);
        }
        this.$api.serialno.doDelete(arr).then(data!=null?data.callback:'')
      },
      // 
      handleAdd: function () {
        this.dialogVisible = true;
        this.operation = true;
        this.dataForm = {
          serialName: '',
          formularRegx: '',
          serialLength: '',
          initValue: '',
          tabName: '',
          secoundName: '',
          createType: '',
          currentValue: '',
          sfbl: '',
          step: '',
          remark: '',
        }
      },
      // 
      handleEdit: function (params) {
        this.dialogVisible = true;
        this.operation = false; // 
        this.dataForm = Object.assign({}, params.row)
      },
      // 
      submitForm: function () {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {

            this.$confirm('', '', {}).then(() => {
              this.editLoading = true;
              this.dialogVisible = false;
              let params = Object.assign({}, this.dataForm);
              this.$api.serialno.save(params).then((res) => {
                  if(res.code == 0) {
                    this.$message({ message: '', type: 'success' })
                  } else {
                    this.$message({message: ', ' + res.msg, type: 'error'})
                  }
                  this.editLoading = false
                  this.$refs['dataForm'].resetFields()
                  this.editDialogVisible = false
                  this.findPage(null)
                })
            })
          }
        })
      },
      // 
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      }
    },
    mounted() {
    }
  }
</script>

<style scoped>

</style>









