<template>
    <el-container class="page-container scrollAllY">
        <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">
          <!---->
          <el-form class="textAlign" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size"  label-position="right">
            <el-collapse class="elCollapseItemNoBoeder elCollapseDict">
              <el-row>
                <el-col :span="2">
                  <el-form-item class="dictInputQueryLabelWidth">
                    <span></span>
                  </el-form-item>
                </el-col>
                <el-col :span="14">
                  <el-form-item  class="dictInputQueryWidth textAlign">
                    <el-date-picker
                      v-model="timeAll"
                      class="widthAll"
                      type="datetimerange"
                      :picker-options="pickerOptions"
                      value-format="yyyy-MM-dd HH:mm:ss" format="yyyy-MM-dd HH:mm:ss"
                      range-separator=""
                      start-placeholder=""
                      end-placeholder=""
                      align="right">
                    </el-date-picker>
                  </el-form-item>
                </el-col>
                <el-col :span="2">
                  <el-form-item class="dictInputQueryLabelWidth">
                    <span></span>
                  </el-form-item>
                </el-col>
                <el-col :span="6">
                  <el-form-item  class="dictInputQueryWidth textAlign">
                    <el-input  v-model="filters.logname" placeholder="" clearable></el-input>
                  </el-form-item>
                </el-col>
              </el-row>
              <el-collapse-item class="elCollapseItemNoBoeder elCollapseItemMarginBottom">
                <el-row>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span></span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.succeed" placeholder="" clearable></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span>ip</span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.ip" placeholder="ip" clearable></el-input>
                    </el-form-item>
                  </el-col>
                  <el-col :span="2">
                    <el-form-item class="dictInputQueryLabelWidth">
                      <span></span>
                    </el-form-item>
                  </el-col>
                  <el-col :span="6">
                    <el-form-item  class="dictInputQueryWidth textAlign">
                      <el-input  v-model="filters.userid" placeholder="" clearable></el-input>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-collapse-item>
            </el-collapse>
            <el-row class="elDeptFormButton dictQueryCenterButton">
              <el-col :span="24">
                <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:tsysloginlog:query" type="primary" @click="findPage(null)"/>
              </el-col>
            </el-row>

          </el-form>
        </el-header>
        <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
          <!---->
          <cb-table  permsDelete="sys:tsysloginlog:remove" :data="pageResult" :columns="columns"  @selectionChange="selectionChange"
                    :isOpen="isOpen" :cbid="cbid"
                    @findPage="findPage" @handleDeleteOther="handleDeleteOther" @handleBatchDelete="handleBatchDelete">
          </cb-table>
        </el-main>
    </el-container>
</template>

<script>
  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import { format } from "@/utils/datetime"
  export default {
    components: {
      CbTable,
      CbButton
    },
    data() {
      return {
        timeAll:[],
        pickerOptions: {
          shortcuts: [{
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit('pick', [start, end]);
            }
          }, {
            text: '',
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit('pick', [start, end]);
            }
          }]
        },
        selections : [],
        isOpen : true,
        cbid : 'loginlogid',
        ids : [], // id
        size: 'small',
        filters: {
          logname:'',
          succeed:'',
          ip:'',
          makeName:'',
        },
        dataForm:{
          logname:'',
          succeed:'',
          ip:'',
          message:'',
          userid:'',
          makeName:'',
        },
        columns: [
          {prop:"logname", label:"", minWidth:100},
          {prop:"userid", label:"", minWidth:100},
          {prop:"createTime", label:"", minWidth:100},
          {prop:"succeed", label:"", minWidth:100},
          {prop:"message", label:"", minWidth:100},
          {prop:"ip", label:"Ip", minWidth:100},
        ],
        pageResult: {},
      }
    },
    methods: {
      // 
      findPage: function (data) {
        if(data !== null) {
          this.pageRequest = data.pageRequest
        }
        let jj={};
        jj.page = this.pageRequest.pageNum; // 
        jj.limit = this.pageRequest.PageSize; // 
        jj.logname = this.filters.logname; // 
        jj.succeed = this.filters.succeed; // 
        jj.ip = this.filters.ip;
        jj.userid = this.filters.userid; // 
        if(this.timeAll){
          jj.beginTime=this.timeAll[0];
          jj.endTime=this.timeAll[1];
        }
        this.$api.loginlog.findPage(jj).then((res) => {
          this.pageResult = res.data;
        }).then(data!=null?data.callback:'');
      },

      selectionChange:function(data){
        var selections = data.selections;
        if (selections.length > 0){
          for(var i=0; i<selections.length; i++){
            var obj = selections[i];
            this.ids.push(obj.loginlogid);
          }
        }
      },

      // 
      handleDeleteOther: function (data) {
        data.vue.clearSelection();
        data.vue.toggleRowSelection(data.obj);
        this.$confirm('', '', {
          type: 'warning',
          closeOnClickModal:false
        }).then((res) => {
          if (data.obj != undefined && data.obj != null){
            this.ids.push(data.obj.loginlogid);
          }
          this.$api.loginlog.remove(this.ids).then((res) => {
            if (res.code == 0){
              this.$message({message: '', type: 'success'});
              this.findPage(null);
            } else {
              this.$message({message: '', type: 'error'});
            }
          });
          this.ids = [];
        }).catch(() => {
          data.vue.toggleRowSelection(data.obj);
        });;
      },
      // 
      handleBatchDelete:function(data){
        /*
        var selections = data.params;
        this.ids = selections.map(item => item.loginlogid).toString().split(",");
        //this.$api.loginlog.remove(this.ids).then(data!=null?data.callback:'');
        this.$api.loginlog.remove(this.ids).then((res) => {
          if (res.code == 0){
            this.$message({message: '', type: 'success'});
            this.findPage(null);
          } else {
            this.$message({message: '', type: 'error'});
          }
        });
        */
      },

      // 
      dateFormat: function (row, column, cellValue, index){
        return format(row[column.property])
      }
    },
    mounted() {
    }
  }
</script>

<style scoped>

</style>



