<template xmlns="element-ui">
  <div class="page-container" >
    <el-container  class="page-container scrollAllY">
      <el-header  class="box_shadows bgcolor  scrollRightYDictMenu " style="height: auto;">

        <!---->
        <el-form class="queryForm" label-width="100px" ref="queryForm" :inline="true" :model="filters" :size="size">
          <el-row class="elCollapseDict">
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <el-input  v-model="filters.roleName" placeholder="" name="roleName"
                    clearable @clear="clearInputValueForQueryRoleName"
                    @keyup.enter.native="keyupEnterForQueryRoleName"
                    @keyup.delete.native="keyupDeleteForQueryRoleName"
                    @keyup.space.native="keyupSpaceForQueryRoleName"
                ></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">

                <!---->
                <cb-popover-tree :data="popupTreeDataForQuery" trigger="click" title="" placeholder=""
                             node-key="id" width="225" content="" inputWidth="" size="mini"
                             :disabled="disabled" defaultExpandedLevel="1"
                             accordion isShowFilterInput  closable readonly
                             @currentChange="currentChange" @handleTagClose="handleTagClose">
                </cb-popover-tree>

              </el-form-item>
            </el-col>
            <el-col :span="2">
              <el-form-item class="dictInputQueryLabelWidth">
                <span></span>
              </el-form-item>
            </el-col>
            <el-col :span="6">
              <el-form-item class="dictInputQueryWidth ">
                <template>
                  <el-select v-model="filters.status" clearable placeholder="" @clear="clearForStatusQuery" @change="selectChangeForQueryStatus" style="width: 100%">
                    <el-option
                      v-for="item in optionsForStatus"
                      :key="item.value"
                      :label="item.label"
                      :value="item.value">
                    </el-option>
                  </el-select>
                </template>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row class="elDeptFormButton dictQueryCenterButton">
            <el-col :span="24">
              <cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:role:query" type="primary" @click="findPage(null)"/>
              <cb-button icon="fa fa-plus" :label="$t('action.add')" perms="sys:role:add" type="primary" @click="handleAdd" />
              <cb-button icon="fa fa-user-o" :label="$t('action.assignUsers')" perms="sys:role:setUser" type="primary" @click="handleAssignUsers" />
            </el-col>
          </el-row>
        </el-form>
      </el-header>
      <el-main class="box_shadows bgcolor  scrollRightYDictMenu tableDict marginBottom">
        <!---->
        <cb-table permsEdit="sys:role:edit" permsDelete="sys:role:remove"
                  ref="cbtable" :data="pageResult" :columns="columns" :border="border"
                  :isOpen="isOpen" :isShow="isShow" @switchChange="switchChange"
                  @findPage="findPage" @pageSizes="pageSizes" @toggleRowSelection="toggleRowSelection"
                  @selectionChange="selectionChange" @handleEdit="handleEdit" @handleDelete="handleDelete" :row-click="rowClick">
        </cb-table>
      </el-main>
    </el-container>
    <!---->
    <el-dialog v-dialogDrag :title="operation?'':''" width="50%" top="7vh" :visible.sync="dialogVisible" :close-on-click-modal="false" @close="dialogClosForSave">
      <el-form :model="dataForm" label-width="100px" :rules="dataRule" ref="dataForm" :size="size" style="display: flex;margin: 15px 15px 0 0">
        <div style="width: 50%;">
          <el-form-item label="ID" prop="roleId" v-if="false">
            <el-input v-model="dataForm.roleId" :disabled="true" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="roleName">
            <el-input v-model="dataForm.roleName" name="dataFormRolName" ref="dataFormRolName" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item label="" prop="orgId" >
            <!---->
            <el-popover placement="bottom-start" width="200" trigger="click">
              <el-tree :data="popupTreeDataForQuery" :props="defaultProps" ref="popupTreeForSave" :default-checked-keys="defaultCheckedKeys" class="cb-el-tree-scroll"
                       @current-change="handleTreeSelectChangeForSave" node-key="id"  :default-expanded-keys="defaultExpandedkeysForQueryOrgId" accordion
              ></el-tree>
              <el-input v-model="parentNameForSave" slot="reference" style="cursor:pointer;" placeholder=""
                        :clearable="clearableForInput" @clear="clearForSave" @focus="focusForQuery" name="orgName"
              ></el-input>
            </el-popover>

            <!---->
            <!--<cb-popover-tree :data="popupTreeDataForQuery" trigger="click" title="" placeholder=""
                             node-key="id" width="225" content="" inputWidth="" size="mini"
                             :disabled="disabled"  readonly  closable defaultExpandedLevel="1"
                             accordion
                             @currentChange="a" @handleTagClose="handleTagClose">
            </cb-popover-tree>
            -->
          </el-form-item>
          <el-form-item prop="menuIdList" style="overflow:scroll; height:360px;overflow-x:hidden;" ref="myForm">
            <!---->
            <el-input placeholder="" v-model="filterTextForMenu" style="margin: 0px 0px;"></el-input>
            <!---->
            <el-tree
              ref="menuTree" node-key="id" show-checkbox   highlight-current :filter-node-method="filterNodeForMenuTree"
              :data="menuIdListData" :props="defaultProps" :default-expanded-keys="defaultExpandedKeysForMenuTree"
              @check-change="getCheckedKeysForMenuTree"
            ></el-tree>
          </el-form-item>
        </div>
        <div style="width: 50%;">
          <el-form-item label="" prop="dataScope" >
            <el-select v-model="dataForm.dataScope" placeholder="" style="width: 100%;" clearable @change="queryScope">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="remark">
            <el-input v-model="dataForm.remark" auto-complete="off" placeholder=""></el-input>
          </el-form-item>
          <el-form-item prop="menuIdList" style="overflow:scroll; height:360px;overflow-x:hidden;" >
            <el-input
              style="margin: 0px 0px;"
              placeholder=""
              v-model="filterleftTree">
            </el-input>
            <el-tree
              class="filter-tree"
              :data="dataleftTree"
              show-checkbox check-strictly accordion
              node-key="id" :default-expanded-keys="defaultExpandedkeysForQueryOrgId"
              @node-click="handleSelectMenuTree"
              @check="checkForSaveOfLeftOrgTree"
              :filter-node-method="filterNodeleft"
              ref="leftTree">
            </el-tree>
          </el-form-item>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click.native="dialogVisible = false">{{$t('action.cancel')}}</el-button>
        <el-button v-if="operation == true" :size="size" type="primary" @click.native="submitFormSaveContinue"  :loading="editLoading">{{$t('action.saveContinue')}}</el-button>
        <el-button :size="size" type="primary" @click.native="submitForm"  :loading="editLoading">{{$t('action.preservation')}}</el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog v-dialogDrag :title="titleForSetRoleUser" width="60%;" top="7vh" style="margin-top: 0px;" :visible.sync="dialogUser" :close-on-click-modal="false" @close="dialogClosForSetUser">
      <div class="tag-group" style="margin:10px; padding: 10px">
        <el-tag v-for="item in itemList" :key="item.roleId" :type="item.type"
               closable @close="tagClose(item.roleId)"
               effect="plain" size="small" style="margin:0px 5px;"> {{ item.roleName }}</el-tag>
      </div>
      <el-form :model="dataForm" label-width="100px" ref="dataForm" :size="size"
               label-position="right" style="height: 500px;display: flex">
        <div style="width: 28%; box-shadow: 1px 1px 3px rgba(0,0,0,0.5);border-radius: 6px;padding: 10px;margin:0px 10px 10px 10px;">
          <!---->
          <el-input size="small" placeholder="" v-model="filterTextForSetRoleUser"
                    style="margin: 10px 0px;">
          </el-input>
          <!---->
          <!--  check-strictly -->
          <el-tree
            style="margin-top: 10px; height: 360px;"
            class="filter-tree cb-el-tree-scroll"
            :data="orgTreeForSetUser"
            show-checkbox
            @check-change="handleNodeClickForSetRoleUser"
            @check="handleNodecheckForSetRoleUser"
            highlight-current :default-expanded-keys="defaultExpandedKeysForOrgTree"
            node-key="id" accordion
            :filter-node-method="filterNodeForSetRoleUser"
            ref="orgTreeForSetRoleUser">
          </el-tree>
        </div>
        <div  style="width: 80%; box-shadow: 1px 1px 3px rgba(0,0,0,0.5);border-radius: 6px;padding: 10px;margin:0px 10px 10px 10px;">
          <div style="height: 90%;margin-top: 10px;">
            <el-form class="toolbarinput" :inline="true" :model="filtersUser" :size="size">
              <el-form-item >
                <el-tooltip class="item" effect="dark" content="" placement="bottom">
                  <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange"></el-checkbox>
                </el-tooltip>
              </el-form-item>
              <el-form-item >
                <el-autocomplete class="inline-input"  v-model="filtersUser.userRealname"  placeholder=""
                  :fetch-suggestions="querySearch" @select="handleSelect" clearable @clear="clearAutocompleteInputForSetRoleUser"
                  @keyup.enter.native="keyupEnterForInputUserRealName" @keyup.delete.native="keyupDeleteForInputUserRealName" @keyup.space.native="keyupSpaceForInputUserRealName"
                  ref="inputForUserRealname" :select-when-unmatched="selectWhenUnmatched"
                ></el-autocomplete>
              </el-form-item>
              <!---->
              <!--<el-form-item><cb-button icon="fa fa-search" :label="$t('action.search')" perms="sys:role:query" type="primary" @click="showUserInfo2"/></el-form-item> -->
            </el-form>

            <div id="cb-el-checkbox-group" style="overflow:scroll; height:350px;overflow-x:hidden;">
              <template>
                <el-checkbox-group v-model="checkboxGroup" @change="handleCheckedUserListChange">
                  <el-checkbox v-for="user in userList" :label="user.userId" :key="user.userId"
                               v-bind:title="':'+user.userRealname"
                               border size="medium" style="width:90px; height:90px; margin: 5px;overflow: hidden">
                    {{user.userRealname}}<img style="width:40px; height:40px; border-radius: 50%; float: left; margin-top: 12px;"  v-bind:src="user.userimg" >
                  </el-checkbox>
                </el-checkbox-group>
              </template>
            </div>
          </div>
          <div style="margin-top: 15px;">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="page"
              :page-sizes="[24, 50, 100, 200]"
              :page-size="limit"
              layout="total, sizes, prev, pager, next, jumper"
              :total="totalCount">
            </el-pagination>
          </div>
        </div>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button :size="size" @click.native="dialogUser = false">{{$t('action.cancel')}}</el-button>
        <el-button :size="size" type="primary" @click.native="setRoleUser"  :loading="editLoading">{{$t('action.preservation')}}</el-button>
      </div>
    </el-dialog>

  </div>
</template>


<script>

  import CbTable from "@/views/Core/CbTable"
  import CbButton from "@/views/Core/CbButton"
  import TableTreeColumn from '@/views/Core/TableTreeColumn'
  import PopupTreeInput from "@/components/PopupTreeInput";
  import {baseUrl} from "../../utils/global";
  import {
    convertTreeData_2,
    getDefaultExpandedKeys,
    setDataProperties
  } from "../../utils/cbUtils";
  import CbPopoverTree from "../Core/CbPopoverTree";

  export default {
    components:{
      CbPopoverTree,
      CbTable,
      CbButton,
      TableTreeColumn,
      PopupTreeInput,
    },
    data() {
      // 
      var validateRoleName = (rule, value, callback) => {
        value = value.trim();
        if (value == null || value == "" || value == undefined){
          callback(new Error(''));
        } else if (value.length > 100){
          callback(new Error('100'));
        } else {
          callback();
        }
      };
      // 
      var validateRoleRemark = (rule, value, callback) => {
        console.log(value);
        if (value != null && value != '' && value != undefined){
          value = value.trim();
        }
        if (value != null && value != '' && value != undefined && value.length > 100){
          callback(new Error('100'));
        } else {
          callback();
        }
      };
      return {

        tableVue : {}, // 
        row : {},
        selections : [], // 
        disabled : false,
        isShowFilterInput : true, // 
        items: [], // tag
        isShowElPopover:false, // 
        optionsForStatus : [{value: '1', label: ''}, {value: '0', label: ''}],
        border : false, // 
        triggerOnFocus : true,
        selectWhenUnmatched : true, //  select 
        restaurants: [], // 
        itemsForSetRoleUser : [],
        itemList : [], // tag
        isShow : true, // Switch 
        isFirst : true,
        checkAll: false, // 
        checkedCities: [],
        isIndeterminate: false, //  indeterminate 
        myResult : [], // 
        orgIdListCheck : [], // 
        okData : [], // 
        isOpen : true, // 
        showRoleName : '', // 
        titleForSetRoleUser : '', // 
        titleForRoleUser : '',
        filterleftTree:'',
        dataleftTree: [],
        defaultCheckedKeys : [], // 
        clearableForInput : true, // popupTreeInput
        pageSizes:[1],
        defaultExpandedKeysForOrgTree : [], // 
        defaultExpandedKeysForMenuTree :["19c786f2bfbf46398e3b495f6c7014b1", "1", "cc3e5f98fb9d4c83bbb29332f4b0d85e"], // 
        clearableForPopupTreeInput : true, //el-inputx
        readonlyForPopupTreeInput : false, // 
        placeholderForOrg : '',
        defaultProps : [], // 
        dataFormRules : [], // 
        menuIdListData:[], // 
        popupTreeDataForQuery : [], // 
        defaultExpandedkeysForQueryOrgId : [], // 
        parentIdForQuery : 0, // 
        parentNameForQuery : '', // 
        popupTreeDataForSave : [], // 
        defaultExpandAllForSave : true, // 
        parentIdForSave : 0, // 
        parentNameForSave : '', // 
        popupTreePropsForQuery: {
          label: 'name',
          children: 'list'
        },
        orgTreeForSetUser: [], // 

        roleIdList : [], // ID
        roleList : [],
        userList : [], // --
        page : 1, // 
        limit : 24, // 
        totalCount : 0, // 
        myParams : {}, // 
        checkboxGroup : [], // ID
        allCheckBoxGroup : [], // ID
        roleUser : { // javebean
          roleIdList : [],
          userIdList : []
        },
        isSetRoleUser : false, // 
        filterTextForMenu : '',
        filterTextForSetRoleUser:'',
        sizeForSetRoleUser : 'medium',
        size: 'small',
        filters: { // 
          orgId : null, // ID
          status : null // 
        },
        filtersUser: { // 
          username: '',
          userRealname : '',
        },
        columns: [
          {prop:"roleName", label:"", minWidth:120},
          {prop:"orgId", label:"", minWidth:120},
          {prop:"createTime", label:"", minWidth:120, formatter:this.dateFormat},
          {prop:"remark", label:"", minWidth:120},
        ],
        pageResult: {},
        operation: false, // true:, false:
        dialogVisible: false, // 
        dialogUser: false,
        editLoading: false,
        dataRule: {
          roleName: [{ required: true, validator: validateRoleName, trigger: 'blur' } ],
          dataScope: [{ required: true, message: '', trigger: 'blur' } ],
          remark : [{required: false, validator: validateRoleRemark, trigger: 'blur'}],
        },
        // 
        dataForm: {
          roleId:'', // id
          roleName:'', // 
          orgId:'', // 
          dataScope:'', // 
          orgIdList : [],
          menuIdList:[], // ID
          remark:'', // 
          roleType : '', // 
          status : '1' ,// 10
          userIdList:'',
        },
        statevalue: [{
          statevalueValue: '',
          label: ''
        }, {
          statevalueValue: '',
          label: ''
        }],
        queryvalue: [{
          queryValue: '',
          label: ''
        }, {
          queryValue: '',
          label: ''
        }],
        options: [{
          value: '1',
          label: ''
        }, {
          value: '2',
          label: ''
        }, {
          value: '3',
          label: ''
        }, {
          value: '4',
          label: ''
        }, {
          value: '5',
          label: ''
        }],
        value: ''
      }
    },
    watch: {
      // 
      filterTextForMenu(val) {
        this.$refs.menuTree.filter(val);
      },
      // 
      filterleftTree(val) {
        this.$refs.leftTree.filter(val);
      },
      // 
      filterTextForSetRoleUser(val) {
        this.$refs.orgTreeForSetRoleUser.filter(val);
      },
    },
    methods: {

      // 
      filterNodeForMenuTree(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      filterNodeleft(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },

      // 
      filterNodeForSetRoleUser(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },

      // todo --------------------  begin --------------------
      //  begin
      clearInputValueForQueryRoleName(){
        this.filters.roleName = null;
        this.findPage();
      },
      keyupEnterForQueryRoleName(event){ // 
        if(event.target.value != "" && event.target.value != null && event.target.value != undefined){
          this.findPage();
        }
      },
      keyupDeleteForQueryRoleName(){ // 
        if (this.filters.roleName == null || this.filters.roleName == "" || this.filters.roleName.length == 0){ // 
          this.findPage();
        }
      },
      keyupSpaceForQueryRoleName(){ // 
        console.log("");
      },
      //  end

      //  clearable 
      clearPopupTreeForQuery(){
        this.filters.orgId = "";
        this.findPage(null);
      },
      // 
      focusForQuery(event){
        event.target.setAttribute("readonly", "readonly")
      },

      // 
      selectChangeForQueryStatus(val){
        this.filters.status = val;
        this.findPage();
      },
      // clear,
      clearForStatusQuery(){
        //this.filters.status = null; // 
        delete this.filters.status; // 
        this.findPage(null);
      },
      // todo --------------------  end --------------------

      // 
      findPage: function (data) {
        if(data !== null && data != undefined) {
          this.pageRequest = data.pageRequest;
        }
        let jj={};
        jj.page=this.pageRequest.pageNum; // 
        jj.limit=this.pageRequest.PageSize; // 
        jj.roleName=this.filters.roleName; // 
        jj.orgId=this.filters.orgId; // 
        jj.status = this.filters.status; // 
        if (jj.roleName != "" && jj.roleName != undefined && jj.roleName != null){
          jj.roleName = jj.roleName.trim(); // 
        }
        this.$api.role.findPage(jj).then((res) => {
          if (res.code == 0){
            if (res.data.list.length > 0 && res.data.list != null){
              var list = res.data.list;
              for(var i=0; i<list.length; i++){
                if (list[i].status == "1" || list[i] == 1){
                  //list[i].status = "";
                } else {
                  //list[i].status = "";
                }
              }
            }
            this.pageResult = res.data;
          }
        }).then(data!=null?data.callback:'');
      },

      // todo --------------------  begin --------------------
      // 
      handleAdd: function () {
        this.dialogVisible = true;
        this.operation = true; // 
        this.dataForm = {
          roleId:'', // id
          roleName:'', // 
          orgId:'', // 
          dataScope:'', // 
          menuIdList:[], // ID
          orgIdList:[],
          remark:'', // 
          roleType : '', // 
          status : '1' // 10
        };
        if (this.dataForm.dataScope=="5"){
          this.queryAddOrgTree();
        } else {
          this.dataleftTree = [];
        }
        // 
        if (this.$refs.menuTree != undefined){
          this.$refs.menuTree.setCheckedNodes([]);
        }
        // 
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
        // 
        this.$nextTick(() => {
          // 
          this.$refs.dataFormRolName.focus();
          //this.$refs.dataFormRolName.$el.getElementsByTagName("input")[0].focus();
          // 
          document.getElementsByName("orgName")[0].style.cursor = "pointer";
        })
      },

      // 
      handleEdit: function (params) {

        this.tableVue = params.tableVue;
        this.tableVue.clearSelection(); // 
        this.tableVue.toggleRowSelection(params.row); // 

        this.dialogVisible = true; // 
        this.operation = false; // 
        this.dataForm = Object.assign({}, params.row);
        // 
        if (this.dataForm.dataScope=="5"){
          this.queryAddOrgTree();
        } else {
          this.dataleftTree = [];
        }
        // 
        if ("" == this.dataForm.status){
          this.dataForm.status = 1;
        } else if ("" == this.dataForm.status){
          this.dataForm.status = 0;
        }
        // 
        if (this.operation == false){
          var data = {roleId:this.dataForm.roleId};
          this.$api.role.selectObjectById(data).then((res) => {
            // 
            if (res.data.tsysRole.menuIdList != null && res.data.tsysRole.menuIdList.length > 0){
              // IDID
              this.dataForm.menuIdList = res.data.tsysRole.menuIdList;
              this.dataForm.orgIdList = res.data.tsysRole.orgIdList;
              var _arr = [];
              for(var i=0; i<res.data.tsysRole.menuIdList.length; i++){
                var _obj = {id:res.data.tsysRole.menuIdList[i]};
                _arr.push(_obj);
                this.$refs.menuTree.setChecked(_arr[i].id, true, false); // setChecked
              }
            }
            //  todo
            this.dataForm.orgId = res.data.tsysRole.orgId; // dataFormorgId
            this.parentNameForSave = res.data.orgName; //
            // 
            if (res.data.tsysRole.dataScope == "5"){
              var _arr = res.data.tsysRole.orgIdList;
              this.$refs.leftTree.setCheckedKeys(_arr); // 
            }
          });
        }
        // 
        if(this.$refs['dataForm']){
          this.$refs['dataForm'].clearValidate();
        }
      },

      // 
      dialogClosForSave(){
        this.clearForSave();
        this.$refs.menuTree.setCheckedKeys([]); // 
        for(let i=0; i<this.selections.length; i++){
          this.tableVue.toggleRowSelection(this.selections[i]);
        }
      },

      //  
      submitForm: function () {
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.dialogVisible = false; // false
              this.editLoading = true;
              if (this.dataForm.dataScope != "5") { // 
                this.dataForm.orgIdList = [];
              }
              let params = Object.assign({}, this.dataForm);
              this.$api.role.save(params).then((res) => {
                this.editLoading = false
                if(res.code == 0) {
                  this.$message({ message: '', type: 'success' })
                  this.dialogVisible = false
                  this.$refs['dataForm'].resetFields();
                  // 
                  this.parentNameForSave = '';
                  // 
                  this.dataleftTree = [];
                  this.$refs.leftTree.setCheckedKeys([]);
                } else {
                  this.$message({message: ', ' + res.msg, type: 'error'})
                }
                this.findPage(null)
              });
            })
          }
        })
      },
      // 
      submitFormSaveContinue(){
        this.$refs.dataForm.validate((valid) => {
          if (valid) {
            this.$confirm('', '', {}).then(() => {
              this.dialogVisible = false;
              this.editLoading = true;
              if (this.dataForm.dataScope != "5") { // 
                this.dataForm.orgIdList = [];
              }
              let params = Object.assign({}, this.dataForm);
              this.$api.role.save(params).then((res) => {
                this.editLoading = false
                if(res.code == 0) {
                  this.$message({ message: '', type: 'success' })
                  this.dialogVisible = true; // false
                  this.$refs['dataForm'].resetFields();
                  // 
                  this.parentNameForSave = '';
                  // 
                  this.dataleftTree = [];
                  this.$refs.leftTree.setCheckedKeys([]);
                } else {
                  this.$message({message: ', ' + res.msg, type: 'error'})
                }
              });
            })
          }
        })
      },

      // , clearable 
      clearForSave(){
        this.parentNameForSave = null; // input
        this.parentIdForSave = null; // input
        this.dataForm.orgId = null; // 
      },

      //  args
      checkForSaveOfLeftOrgTree : function(data, args){
        this.dataForm.orgIdList = args.checkedKeys; // ID
      },

      // 
      queryScope:function(){
        if (this.dataForm.dataScope=="5"){
          //this.$refs.leftTree.setCheckedKeys([]);
          this.queryAddOrgTree();
        }else {
          this.dataleftTree = [];
          this.$refs.leftTree.setCheckedKeys([]);
          if (this.operation){ // 
            this.dataForm.orgIdList = [];
          }
        }
      },
      // node-click
      handleSelectMenuTree: function(data){},

      // el-popoverhel-tree
      handleTreeSelectChangeForSave (data, node){
        this.parentIdForSave = data.id;
        this.parentNameForSave  = data.name
        this.dataForm.orgId = data.id; // javabean
      },

      //  key 
      getCheckedKeysForMenuTree(data, isChecked, a){
        var a = this.$refs.menuTree.getCheckedKeys(); // key()
        var b = this.$refs.menuTree.getHalfCheckedKeys(); //  key key
        var c = a.concat(b);
        this.dataForm.menuIdList = c;
      },

      // todo --------------------  end --------------------

      // 
      handleDelete: function (data) {
        let arr = [];
        for (let i=0; i<data.params.length; i++){
          arr.push(data.params[i].id);
        }
        this.$api.role.doDelete(arr).then(data!=null?data.callback:'')
      },

      // -	selection
      selectionChange : function(data){
        this.tableVue = data.vue;
        this.selections = data.selections;
        if (data.selections.length > 0 && data != undefined && data.selections != null){
          var ids = data.selections.map(item => item.id).toString();
          var status = data.selections.map(item => item.status).toString();
          this.showRoleName = data.selections.map(item => item.roleName).toString();
          this.roleIdList = ids.split(","); // 
          this.roleList = data.selections;
          this.itemList = data.selections; // tag
        } else { // 
          this.roleIdList = [];
        }
      },

      // todo --------------------  begin --------------------
      // 
      handleAssignUsers:function(data){
        if (this.roleIdList == null || this.roleIdList.length <= 0){
          this.$message({message: "", type: 'warning'});
          return false;
        } else if (this.roleIdList.length > 8){
          this.$message({message: "", type: 'warning'});
          return false;
        }
        if (this.roleList.length > 0){
          for(var i=0; i<this.roleList.length; i++){
            if (this.roleList[i].status == 0 || this.roleList[i].status == "0"){
              this.$message({message: ""+this.roleList[i].roleName+"", type: 'warning'});
              return false;
            }
          }
        }
        this.dialogUser = true; // true:,false:
        this.isSetRoleUser = true; // 
        this.dataForm = {username: '', remark: ''};
        // 
        if (this.itemList.length > 0){
          var css = ["", "success", "info", "danger", "warning"];
          for(var i=0; i<this.itemList.length; i++){
            this.itemList[i].type = css[Math.floor(Math.random()*3)];
          }
        }
        // 
        this.myParams.pageNum = this.page;
        this.myParams.pageSize = this.limit;
        // 
        this.showUserInfo2();
      },

      // 
      dialogClosForSetUser(){
        // 
        this.page = 1;
        // 
        this.userList = [];
        this.okData = [];
        this.orgIdListCheck = [];
        // 
        this.checkboxGroup = [];
        // 
        this.filterTextForSetRoleUser = "";
        // 
        this.$refs.orgTreeForSetRoleUser.setCheckedKeys([]);
        // 
        this.myParams.orgId = "";
        this.myParams.orgIds = null;
        // 
        var name = this.$refs.orgTreeForSetRoleUser.$el.getAttribute("class").split(" ");
        if (name.length > 0){
          for(var i=0; i<name.length; i++){
            if (name[i] == "el-tree--highlight-current"){
              this.$refs.orgTreeForSetRoleUser.$el.classList.remove(name[i]);
            }
          }
        }
      },

      // 
      setRoleUser : function(){
        if (this.checkboxGroup == null || this.checkboxGroup.length <= 0){
          //this.$message({ message: "", type: 'warning' });
          //return false;
        }
        this.roleUser.roleIdList = this.roleIdList; // ID
        this.roleUser.userIdList = this.checkboxGroup; // ID
        this.$api.role.saveRoleUser(this.roleUser).then((res) => {
          if (res.code == 0){
            this.checkboxGroup = []; // 
            this.dialogUser = false; // 
            this.$message({ message: '', type: 'success' });
          } else {
            this.$message({ message: res.msg, type: 'error' });
          }
        });
      },

      // -
      handleSizeChange(val) {
        this.limit = val;
        this.myParams.pageNum = this.page;
        this.myParams.pageSize = this.limit;
        this.checkboxGroup = [];
        // 
        this.showUserInfo2();
      },

      // -
      handleCurrentChange(val) {
        this.page = val;
        this.myParams.pageNum = this.page;
        this.myParams.pageSize = this.limit;
        this.checkboxGroup = [];
        // 
        this.showUserInfo2();
      },


      // 
      handleNodecheckForSetRoleUser(data, arg){
        var orgIdList = this.$refs.orgTreeForSetRoleUser.getCheckedKeys();
        var ids = '';
        for(var i=0; i<orgIdList.length; i++){
          ids += orgIdList[i]+",";
        }
        ids = ids.substring(0, ids.length-1);
        this.myParams.orgIds = ids; // ID
        // 
        if (orgIdList.length <= 0){
          this.myParams.orgId = "";
          this.myParams.orgIds = null;
          this.checkboxGroup = [];
          this.page = 1;
          this.myParams.pageNum = 1;
          this.myParams.pageSize = this.limit;
        }
        this.showUserInfo2();
      },
      // 
      handleNodeClickForSetRoleUser (data, isChecked, arg2){},

      // 
      handleCheckAllChange(val) {
        this.checkedCities = val ? this.allCheckBoxGroup : [];
        this.checkboxGroup = this.checkedCities;
        this.isIndeterminate = false;
      },
      // 
      handleCheckedUserListChange(value) {
        let checkedCount = this.checkboxGroup.length;
        this.checkAll = checkedCount === this.userList.length;
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.userList.length; // 
      },
      // 
      querySearch:function(queryString, cb){
        var restaurants = this.restaurants; // 
        var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
        //  callback 
        cb(results);
      },
      createFilter(queryString) {
        return (restaurant) => {
          return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
        };
      },
      handleSelect:function(item){ // item
        // 
        if (item.userRealname != this.myParams.userRealname && this.filtersUser.userRealname != "" && this.myParams.userRealname != this.filtersUser.userRealname){
          this.myParams.userRealname = item.userRealname;
          this.showUserInfo2();
        }
      },
      // 
      keyupEnterForInputUserRealName:function(event){
        return false; // select-when-unmatchedtrue
        if (this.filtersUser.userRealname != null && this.filtersUser.userRealname != ""){
          this.myParams.userRealname = this.filtersUser.userRealname;
          this.showUserInfo2();
          // todo ---
          var elScrollbar = this.$refs.inputForUserRealname.$el.getAttribute("class");
          var a = this.$el.getElementsByClassName("el-autocomplete-suggestion");
          var element = document.querySelector(".popper-class");
          element.style.position = 'static';
        }
      },
      // 
      keyupDeleteForInputUserRealName:function(){
        if (this.filtersUser.userRealname == null || this.filtersUser.userRealname == ""){ // 
          this.myParams.userRealname = "";
          this.showUserInfo2();
        }
      },
      // 
      keyupSpaceForInputUserRealName:function(){
        // todo 
      },
      // x
      clearAutocompleteInputForSetRoleUser:function(){
        this.myParams.userRealname = "";
        this.showUserInfo2();
      },

      // 
      findPerms: function(){
        this.$api.role.queryPerms().then((res) => {
          // 
          let attr = {
            id: 'menuId',
            parentId: 'parentId',
            parentName: 'parentName',
            name: 'name',
            rootId: "-1"
          };
          //let treeData = this.$api.role.toTreeData(res.data, attr); // 
          let data = setDataProperties("menuId", "name", res.data, "parentId", "parentName");
          let treeData = convertTreeData_2(data);
          this.menuIdListData = treeData; // 
          this.defaultExpandedKeysForMenuTree = getDefaultExpandedKeys(treeData, 2);
        });
      },

      // 
      queryAddOrgTree : function () {
        this.$api.role.orgTree().then(res => {
          let data = setDataProperties("orgId", "orgName", res.data, "parentId", "parentName");
          let treeData = convertTreeData_2(data);
          this.dataleftTree = treeData
          if (this.dataForm.orgIdList != null && this.dataForm.orgIdList.length > 0){
            this.$refs.leftTree.setCheckedKeys(this.dataForm.orgIdList);
          }
        });
      },
      // todo --------------------  end --------------------

      // 
      queryOrgTree : function () {
        this.$api.role.orgTree().then(res => {
          let data = setDataProperties("orgId", "orgName", res.data, "parentId", "parentName");
          let treeData = convertTreeData_2(data);
          if(treeData.length > 0){
            for(var i=0; i<treeData.length; i++){
              this.defaultExpandedkeysForQueryOrgId.push(treeData[i].id); // 
            }
          }
          this.popupTreeDataForQuery = treeData; // 
          this.popupTreeDataForSave = treeData; // 
          this.orgTreeForSetUser = treeData; // 
          this.defaultExpandedKeysForOrgTree = getDefaultExpandedKeys(treeData);
          this.dataleftTree = treeData; // 
        });
      },
      // 
      currentChange(data, node){
        this.filters.orgId = data.id;
        this.findPage(); // 
      },
      // tag
      handleTagClose:function(tag){
        this.filters.orgId = null; // 
        this.findPage();
      },

      rowClick(){},
      inputFocus(e){},

      // switch
      switchChange(result){
        var val = result.e; // 0:1
        var roleId = result.data.roleId; // ID
        var obj = {
          roleId : roleId,
          status : val
        };
        this.$api.role.updateStatus(obj).then((res) => {
          if (res.code == 0){
            if (val == 0){
              this.$message({ message: '', type: 'warning' });
            } else {
              this.$message({ message: '', type: 'success' });
            }
          } else {
            if (val == 0){
              this.$message({ message: '', type: 'warning' });
            } else {
              this.$message({ message: '', type: 'success' });
            }
          }
        });

      },
      toggleRowSelection(result){
        this.tableVue = result.vue; // 
      },
      tagClose(roleId){
        if (this.itemList.length == 1){
          this.$message({ message: '', type: 'warning' });
          return false;
        }
        // 
        if (this.selections.length > 0){
          for(var i=0; i<this.selections.length; i++){
            if (this.selections[i].id == roleId){
              this.tableVue.toggleRowSelection(this.selections[i]);
            }
          }
        }
        // x
        for(var i=0; i<this.itemList.length; i++){
          if (this.itemList[i].roleId == roleId){
            this.itemList.splice(i, 1);
          }
        }
        //
        this.roleUser.roleIdList.splice(this.roleUser.roleIdList.indexOf(roleId), 1); // 
      },
      showUserInfo2(){
        // 
        this.myParams.status = 1;
        this.myParams.userRealname = this.filtersUser.userRealname;
        // 
        this.$api.role.queryUserInfo(this.myParams).then((res) => {
          if (res.code == 0){
            this.userList = []; // 
            this.checkboxGroup = []; // 
            this.allCheckBoxGroup = [];
            this.page = res.data.currPage; // 
            this.limit = res.data.pageSize; // 
            this.totalCount = res.data.totalCount; // 
            var userList = res.data.list; // 
            //  begin
            if (userList != null && userList.length > 0){
              for(var i=0; i<userList.length; i++){
                // 
                if (userList[i].userimg != null && userList[i].userimg != ""){
                  if (userList[i].userimg.indexOf("http") == 0){

                  } else {
                    userList[i].userimg = baseUrl + "/uploads/sys-user-img/" + userList[i].userimg;
                  }
                } else {
                  //userList[i].userimg = "../../assets/my/logo.png"; // 
                  userList[i].userimg = require("@/assets/user.png");
                }
                // 
                if (userList[i].userRealname == "" || userList[i].userRealname == null){
                  userList[i].userRealname = userList[i].username;
                }
                // 
                if(JSON.stringify(this.userList).indexOf(JSON.stringify(userList[i])) == -1){
                  this.userList.push(userList[i]);
                }
                if (JSON.stringify(this.allCheckBoxGroup).indexOf(JSON.stringify(userList[i].userId)) === -1){
                  this.allCheckBoxGroup.push(userList[i].userId);
                }
              }
            }
            //  end
            //  begin
            if (this.isSetRoleUser && this.roleIdList.length > 0 && this.roleIdList != null){
              // 
              if (this.roleIdList.length >= 2){
                this.$api.role.setUser(this.roleIdList).then((res) => {
                  if (res.intersection.length > 0 && res.intersection != null){
                    for (var i=0; i<res.intersection.length; i++){
                      if (JSON.stringify(this.checkboxGroup).indexOf(res.intersection[i]) == -1){
                        // 
                        this.checkboxGroup.push(res.intersection[i]);
                      }
                    }
                  }
                  if (this.checkboxGroup.length == this.userList.length){
                    this.checkAll = true;
                    this.isIndeterminate = false;
                  } else {
                    this.checkAll = false;
                    this.isIndeterminate = true;
                  }
                });
              } else { // 
                this.$api.role.setUser(this.roleIdList).then((res) => {
                  var list = res.data[0].userIdList;
                  for(var i=0; i<list.length; i++){
                    for(var j=0; j<this.userList.length; j++){
                      if (list[i] == this.userList[j].userId){
                        if (JSON.stringify(this.checkboxGroup).indexOf(list[i]) === -1){
                          this.checkboxGroup.push(list[i]);
                        } else {
                          //console.log(this.checkboxGroup[i]);
                        }
                      }
                    }
                  }

                  // todo 
                  for(var i=0; i<this.checkboxGroup.length; i++){
                    for(var j=0; j<this.userList.length; j++){
                      if (this.checkboxGroup[i] == this.userList[j].userId){

                      } else {

                      }
                    }
                  }
                  if (this.checkboxGroup.length == this.userList.length && this.userList.length != 0){
                    this.checkAll = true;
                    this.isIndeterminate = false;
                  } else {
                    this.checkAll = false;
                    this.isIndeterminate = true;
                  }
                });
              }
              //  end
            }
            // input
            this.restaurants = [];
            for(var i=0; i<this.userList.length; i++){
              this.userList[i].value = this.userList[i].userRealname;
              this.restaurants.push(this.userList[i]);
            }
          }
        });
      },
      todoSomething(){
        var width = window.screen.width; // 
        var height = window.screen.height; // 
        if (width == 1920 && height == 1080){

        } else if(width <= 1440 && height < 900){

        }
      },

    },
    mounted() {
      this.queryOrgTree(); // 
      this.findPerms(); // 
      this.todoSomething();
      //console.log(this.$refs);
      //console.log(this.$el);
      //console.log(document.getElementsByName("roleName"));

    },

  }
</script>
