/**
 * id,name, 
 * @param id ID
 * @param name 
 * @param data 
 * @param parentId ID
 * @param parentName 
 * @returns {*}
 */
export function setDataProperties (id, name, data, parentId, parentName) {
  if (data.length > 0){
    data.forEach((obj, i) => {
      obj.id = obj[id];
      obj.cbid = obj[id];
      obj.name = obj[name];
      obj.label = obj[name];
      if (parentId != undefined && parentId != null && parentId != ""){
        obj.parentId = obj[parentId];
      } else if (parentName != undefined && parentName != null && parentName != ""){
        obj.parentName = obj[parentName];
      }
    });
  }
  return data;
}

/**
 * 
 * @param pid 
 * @param data ()
 * @returns {Array}
 */
export function convertTreeData_1(pid, data) {
  function tree(id) {
    let arr = []
    data.filter(item => {
      return item.parentId === id;
    }).forEach(item => {
      arr.push({
        cbid: item.id,
        id: item.id,
        name: item.name,
        label: item.name,
        parentId: item.parentId,
        parentName: item.parentName,
        children: tree(item.id)
      });
    });
    return arr;
  }
  return tree(pid)  // idnull0
}

/**
 * 
 * @param arr
 * @returns {Array}
 */
export function convertTreeData_2(arr) {
  //   children,
  arr.forEach(function (item) {
    delete item.children;
  });
  let map = {}; // map
  arr.forEach(i => {
    map[i.id] = i; // id 
  });
  let treeData = [];
  arr.forEach((child, index) => {
    const mapItem = map[child.parentId]; // parentIdmap
    if (mapItem) { // 
      // : maparrarrmapItemarr,
      (mapItem.children || ( mapItem.children = [] )).push(child); // mapItemchildren, , children[]
    } else { // 
      treeData.push(child);
    }
  });
  return treeData;
}

/**
 * 
 * @param data
 */
export function getDefaultExpandedKeys (data, level) {
  if (data == null || data.length == 0){
    return false;
  }
  if (level == "" || level == null || level == undefined){
    level == 1;
  }
  let defaultExpandedKeys = [];
  for(let i=0; i<data.length; i++){
    defaultExpandedKeys.push(data[i].id); // 
    if (level == 2 || level == "2"){
      let children = data[i].children;
      if (children != null && children.length > 0 && children != undefined){
        for (let j=0; j<children.length; j++){
          defaultExpandedKeys.push(children[j].id);
        }
      }
    }
  }
  return defaultExpandedKeys;
}




