<template>
  <el-popover
    v-model="isShowElPopover"
    ref="popover"
    :trigger="trigger"
    :placement="placement"
    :title="title"
    :width="width"
    :content="content"
    :disabled="disabled"
  >

    <el-input slot="reference" :placeholder="placeholder" :readonly="readonly"
              @focus="focus"
              :width="inputWidth"
              :clearable="clearable" @clear="clear" v-model="inputValue" name="inputName">
      <el-tag slot="prefix" :size="size"
              v-for="item in items"
              :key="item.id"
              :type="item.type"
              :id="item.id" :cbid="item.id"
              :label="item.label"
              effect="plain"
              :closable="closable" @close="handleTagClose" @click="handleTagClick"
              :isMultiShowTag="isMultiShowTag"
              style="margin-left:3px; cursor: pointer"
      >{{ item.label }}</el-tag>
    </el-input>

    <el-input v-if="isShowFilterInput" placeholder="" v-model="filterText"></el-input>
    <el-tree
      ref="tree" :highlight-current="highlightCurrent" :show-checkbox="showCheckbox"
      class="filter-tree cb-el-tree-scroll"
      :data="data"
      :props="defaultProps"
      :default-expanded-keys="defaultExpandedKeys"
      :defaultExpandedLevel="defaultExpandedLevel"
      :accordion="accordion"
      :empty-text="emptyText"
      :nodeKey="nodeKey"
      :filter-node-method="filterNode"
      @current-change="currentChange">
    </el-tree>
  </el-popover>
</template>

<script>
  export default {
    name: "CbPopoverTree",
    data(){
      return {

        defaultExpandedKeys : [], // 
        isShowElPopover : false, //  Popover  true 
        selections : [], // tag

        // input
        filterText: '',
        // tag:[{"type":"", "label",:""}, {"type":"success", "label",:""}]
        items : [],
      }
    },
    props : {

      // todo ---------------------  el-input  begin ---------------------
      placeholder : {
        type: String,
        default: ''
      },
      // 
      clearable : {
        type: Boolean,
        default: true
      },
      readonly : {
        type: Boolean,
        default: false
      },
      inputValue : { // 
        type: String,
        default: ''
      },
      inputWidth : {
        type: String,
        default: '50px'
      },
      // todo  el-input  end

      // todo ---------------------  el-tag  begin ---------------------
      // 
      isMultiShowTag : {
        type : Boolean,
        default : false
      },
      closable : {
        type : Boolean,
        default : false
      },
      // mediumsmallminisize
      size:{
        type : String,
        default : ''
      },
      // todo  el-tag  end

      // todo ---------------------  el-tree  begin ---------------------
      // input
      isShowFilterInput : {
        type : Boolean,
        default : false
      },
      // ,
      data : Array,
      // 
      defaultProps : {
        children: 'children',
        label: 'label'
      },
      // 12,
      defaultExpandedLevel : {
        type: String,
        default: '1'
      },
      // 
      accordion : {
        type : Boolean,
        default : false
      },
      // 
      emptyText : {
        type: String,
        default: ''
      },
      // 
      nodeKey : {
        type: String,
        default: 'id'
      },
      // 
      showCheckbox : {
        type : Boolean,
        default : false
      },
      highlightCurrent : {
        type : Boolean,
        default : false
      },

      // todo --------------------- el-popover  begin ---------------------

      // click/focus/hover/manual  click
      trigger : {
        type: String,
        default: 'click'
      },
      // top/top-start/top-end/bottom/bottom-start/bottom-end/left/left-start/left-end/right/right-start/right-end  bottom
      placement : {
        type: String,
        default: 'bottom'
      },
      // 
      title : {
        type: String,
        default: ''
      },
      //   150px
      width : {
        type: String,
        default: '200'
      },
      //  slot  DOM
      content : {
        type: String,
        default: ''
      },
      // Popover  false:true:
      disabled : {
        type : Boolean,
        default : false
      },
      // todo el-popover end

    },

    watch: {
      filterText(val) {
        this.$refs.tree.filter(val);
      }
    },

    methods: {

      // todo ---------------------  el-input  begin ---------------------
      // input
      focus(){
        this.setDefaultExpandedKeys();
      },
      //  clearable 
      clear(){

      },
      // todo  el-input  end

      // todo ---------------------  el-tag  begin ---------------------
      //  Tag 
      handleTagClick(){
        this.isShowElPopover = true;
      },
      //  Tag 
      handleTagClose:function(tag){
        this.isShowElPopover = false; // x
        var span = tag.target.parentElement; // x
        var id = span.getAttribute("id");
        for(var i=0; i<this.selections.length; i++){
          if (id == this.selections[i].id){
            this.selections.splice(i, 1); // 
          }
        }
        // tag
        for(let i=0; i<this.items.length; i++){
          if (id == this.items[i].id){
            this.items.splice(i, 1);
          }
        }
        if (this.items.length == 0){
          // placeholder
          var ele = document.getElementsByName("inputName")[0];
          ele.setAttribute("placeholder", this.placeholder);
          this.selections = [];
        }
        this.filterText = null; // 
        this.$emit('handleTagClose', {selections:this.selections});
      },
      // todo  el-tag  end

      // todo ---------------------  el-tree  begin ---------------------
      // 
      filterNode(value, data) {
        if (!value) return true;
        return data.label.indexOf(value) !== -1;
      },
      // ,  Node 
      currentChange(data, node){
        if (data.id){
          data.cbid = data.id;
        }
        var ele = document.getElementsByName("inputName")[0];
        let width = ele.style.width;
        ele.setAttribute("placeholder", "");
        var mydate=new Date();
        var css = ["", "success", "info", "warning", "danger"];
        var obj = {"cbid":data.cbid, "id":data.id, "type":css[mydate.getDay()], "label":data.label}; // 
        if (JSON.stringify(this.selections).indexOf(JSON.stringify(obj)) === -1){
          this.selections.push(obj);
        }
        var next = document.getElementsByName("inputName")[0].nextElementSibling;
        if(next != null){ // inputtag
          if (this.isMultiShowTag){ // 
          } else { // tag
            for(var i=0; i<next.childNodes.length; i++){
              var name = next.childNodes[i].innerText;
              for(var j=0; j<this.items.length; j++){
                if(name.trim() == this.items[j].label){
                  this.items.splice(j,1);
                }
              }
            }
            for(var j=0; j<this.items.length; j++){
              for(var k=0; k<this.selections.length; k++){
                if(this.items[j].id == this.selections[k].id){

                }
              }
            }
          }
          if(JSON.stringify(this.items).indexOf(JSON.stringify(obj)) == -1){
            if (this.isMultiShowTag) { // 
              this.items.push(obj);
              /*if (this.items.length >= 2){ // 3
                obj.id = "moreTag";
                obj.label = "+1";
                if (next.children[2] != undefined){
                  return false;
                }
                this.items.push(obj);
                setTimeout(function(){
                  console.log(next.children[2]);
                }, 200);
              }*/
            }
            if(JSON.stringify(this.items).indexOf(JSON.stringify(obj)) == -1){
              this.items.push(obj);
            }
          }
       } else { // 
          if(JSON.stringify(this.items).indexOf(JSON.stringify(obj)) == -1){
            this.items.push(obj);
          }
        }
        //this.$emit('currentChange', {selections:this.selections, node:node});
        if (this.isMultiShowTag){ // tag
          this.$emit('currentChange', this.selections, node);
        } else {
          for(var i=0; i<this.selections.length; i++){
            if (data.id != this.selections[i].id){
              this.selections.splice(i, 1);
            }
          }
          this.$emit('currentChange', this.selections[0], node);
        }
      },
      // todo  el-tree  end

      // 
      setDefaultExpandedKeys(){
        if(this.data.length > 0){
          if (this.defaultExpandedLevel == 1 || this.defaultExpandedLevel == "1"){
            for(var i=0; i<this.data.length; i++){
              this.defaultExpandedKeys.push(this.data[i].id); // 
            }
          } else if (this.defaultExpandedLevel == 2 || this.defaultExpandedLevel == "2"){
            for(var i=0; i<this.data.length; i++){
              for(var j=0; j<this.data[i].children.length; j++){
                this.defaultExpandedKeys.push(this.data[i].children[j].id); // 
              }
            }
          }
        }
      }

    },
    mounted() {
      var ele = document.getElementsByName("inputName")[0];
      // 
      this.setDefaultExpandedKeys();
      ele.style.cursor = "pointer";
      // placeholder
      var placeholder = ele.getAttribute("placeholder");
      this.placeholder = placeholder;
      // 
      var inputWidth = ele.getAttribute("width");
      if (inputWidth == "" || inputWidth == null){
        return false;
      }
      var hasPx = ele.getAttribute("width").substring(ele.getAttribute("width").length-2, ele.getAttribute("width").length);
      if (hasPx != "px"){
        inputWidth = inputWidth+"px";
      }
      ele.style.width = inputWidth;

    },
  }
</script>

<style scoped>

  /**/
  .cb-el-tree-scroll{
    overflow:scroll;
    height:250px;
    overflow-x:hidden;
    width:100%;
  }
  .cb-el-input-scroll{
    overflow:scroll;
    overflow: hidden;
  }

</style>



